* {
    margin: 0;
    border: 0;
}

.outer {
    position: relative;
}

.calendar {
    margin: 60px auto;
    max-width: 100%;
    width: 1280px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    box-shadow: 0px 5px 20px rgb(0 0 0 / 20%), 0px 3px 7px rgb(0 0 0 / 10%);
    border-radius: 8px;
    overflow: hidden;
}

.wrap {
    overflow-x: hidden;
    overflow-y: scroll;
    max-width: 1280px;
    height: 500px;
    border-radius: 8px;
}

thead {
    box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.2);
}

thead th {
    text-align: center;
    width: 100%;
}

header {
    background: #fff;
    padding: 1rem;
    color: rgba(0, 0, 0, 0.7);
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border-radius: 8px 8px 0px 0px;
}

header h1 {
    font-size: 1.25rem;
    text-align: center;
    font-weight: normal;
}

tbody {
    position: relative;
    top: 30px;
}

table {
    background: #fff;
    width: 100%;
    height: 100%;
    border-collapse: collapse;
    table-layout: fixed;
}

.headcol {
    width: 60px;
    font-size: 0.875rem;
    font-weight: 500;
    color: rgba(0, 0, 0, 0.5);
    padding: 0.25rem 0;
    text-align: center;
    border: 0;
    position: relative;
    top: -12px;
    border-bottom: 1px solid transparent;
}

thead th {
    font-size: 1rem;
    font-weight: bold;
    color: rgba(0, 0, 0, 0.9);
    padding: 1rem;
}

thead {
    z-index: 2;
    background: white;
    border-bottom: 2px solid #ddd;
}

tr,
tr td {
    height: 20px;
}

td {
    text-align: center;
}

tr:nth-child(odd) td:not(.headcol):not(.separator) {
    border-bottom: 1px solid #e8e8e8;
}

tr:nth-child(even) td:not(.headcol):not(.separator) {
    border-bottom: 1px solid #eee;
}

.separator {
    border-bottom: 3px solid #d44f4f;
}

tr td {
    border-right: 1px solid #eee;
    padding: 0;
    white-space: none;
    word-wrap: nowrap;
}

tbody tr td {
    position: relative;
    vertical-align: top;
    height: 40px;
    padding: 0.25rem 0.25rem 0 0.25rem;
    width: auto;
}

.secondary {
    color: rgba(0, 0, 0, 0.4);
}

.checkbox {
    display: none;
}

.checkbox + label {
    border: 0;
    outline: 0;
    width: 100px;
    height: 100px;
    background: white;
    color: transparent;
    display: block;
    display: none;
}

.checkbox:checked + label {
    border: 0;
    outline: 0;
    width: 100%;
    height: 100%;
    background: red;
    color: transparent;
    display: inline-block;
}

.event {
    background: #00b4fc;
    color: white;
    border-radius: 2px;
    text-align: left;
    font-size: 0.875rem;
    z-index: 2;
    padding: 0.5rem;
    overflow-x: hidden;
    transition: all 0.2s;
    cursor: pointer;
}

.event:hover {
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
    background: #00b4fc;
}

.event.double {
    height: 200%;
}

.event.triple {
    height: 300%;
}

.event.cuadruple {
    height: 400%;
}

