button.secondary {
    border: 1px solid rgba(0, 0, 0, 0.1);
    background: white;
    padding: 0.5rem 0.75rem;
    font-size: 14px;
    border-radius: 2px;
    color: rgba(0, 0, 0, 0.5);
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    font-weight: 500;
}

button.secondary:hover {
    background: #fafafa;
}

button.secondary:active {
    box-shadow: none;
}

button.secondary:focus {
    outline: 0;
}
